@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;500;900&display=swap);
/* font family */
/* Variable Setting */
:root {
  --main-color: #4e8fc6;
  --index-bg-color: #214869;
  --main-gray: #f0f0f0;
  --font-gray: #999999;
}

/* Basic CSS setting */
* {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  outline: none;
  font-family: 'Noto Sans KR', sans-serif;
}

* button:focus {
  outline: none;
}

body {
  width: 100vw;
  height: 100vh;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  background-image: url(/static/media/main.da8ed2ff.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  /* background: var(--index-bg-color); */
}

.wrap {
  margin-top: 120px;
  width: 1128px;
  height: 643px;

  box-shadow: 5px 45px 30px rgba(21, 32, 95, 1);
}

.main {
  overflow-y: auto;
}

.main::-webkit-scrollbar {
  /* 스크롤바 전체 */
  width: 5px;
  height: 15px;
}

.main::-webkit-scrollbar-thumb {
  /* 스크롤바 막대 */
  background: #4e8fc6;
  background: var(--main-color);
  border-radius: 15px;
}
.main::-webkit-scrollbar-track {
  /* 스크롤바 막대외부 */
  background-color: transparent;
}

/* 태블릿 */
@media screen and (max-width: 1024px) {
  .wrap {
    width: 750px;
  }
}

.inner h2,
h3 {
  color: var(--main-color);
}

.inner .introheader {
  height: 150px;
  padding: 30px;
  background-color: #151747;
}

.inner .introheader h2 {
  font-size: 30px;
}

.inner .introheader p {
  position: relative;
  color: white;
  display: inline-block;
  height: 21px;
  overflow: hidden;
  -webkit-animation: typing 8s steps(40, end) infinite;
          animation: typing 8s steps(40, end) infinite;
}

.inner .introheader p::after {
  position: absolute;
  display: block;
  content: "";
  -webkit-animation: cursor 0.5s step-end infinite;
          animation: cursor 0.5s step-end infinite;
}

.inner .introskills h3,
.inner .introprojects h3,
.inner .introblog h3 {
  font-size: 18px;
}

.inner .introskills p,
.inner .introprojects p,
.inner .introblog p {
  font-size: 14px;
}

.inner .introskills p,
.inner .introblog p {
  color: var(--font-gray);
}

/* ------------------ 스킬셋 ------------------*/
.inner .introskills {
  height: 350px;
  padding: 30px;
}

.inner .introskills .skillset .skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 226px;
  height: 150px;
  border-radius: 15px;
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
  position: relative;
}

.skillset .skills img {
  position: absolute;
  top: -38px;
  left: 72px;
  width: 80px;
  height: 80px;
}

/* ------------------------ 프로젝트------------------------ */
.inner .introprojects {
  height: 430px;
  background-color: var(--main-gray);
  padding: 30px;
}

.inner .introprojects .projectset .projectbox {
  width: 450px;
  height: 240px;
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
  background-color: #fff;
  position: relative;
}

.projectset .projectbox .pro_caption {
  position: absolute;
  color: #fff;
  top: 0px;
  width: 450px;
  height: 240px;
  background-color: rgba(11, 37, 77, 0.8);
  opacity: 0;
  transition: all 0.2s ease-in-out;
  z-index: 10;
}

.projectset .projectbox .pro_caption p {
  font-size: 14px;
  text-align: right;
  margin-bottom: 10px;
}

.projectset .projectbox .pro_caption p:first-child {
  font-weight: bold;
  font-size: 20px;
}

.projectset .projectbox .pro_caption button {
  font-weight: bold;
}

.projectset .projectbox .pro_caption button:hover {
  color: var(--main-color);
  border-bottom: 1px solid var(--main-color);
}

.projectset .projectbox:hover .pro_caption {
  opacity: 1;
  width: 225px;
  transform: translateX(113px);
}

/* ------------------------ 블로그 ------------------------ */
.inner .introblog {
  height: 490px;
  padding: 30px;
}

.inner .introblog button {
  width: 270px;
  height: 40px;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: #6bba86;
}

.inner .introblog .blogbox .blogentry {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 10px;
  margin-bottom: 5px;
  width: 450px;
  height: 250px;
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
}

.blogbox .blogentry .sum_img {
  width: 100%;
  height: 150px;
}

.blogbox .blogentry p {
  width: 350px;
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  line-height: 1.2;
  text-align: left;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.blogbox .blogentry .blog_caption {
  border-top: 1px solid var(--main-gray);
}

/* 태블릿 */
@media screen and (max-width: 1024px) {
  .inner .introprojects .projectset .projectbox {
    margin-bottom: 20px;
  }

  .inner .introprojects {
    height: 700px;
  }

  .inner .introblog {
    height: 490px;
  }

  .inner .introblog .blogbox {
    width: 540px;
  }

  .inner .introblog .blogbox .blogentry {
    width: 250px;
  }

  .blogbox .blogentry p {
    width: 200px;
    height: 50px;
    -webkit-line-clamp: 3;
  }

  .blogbox .blogentry .sum_img {
    height: 130px;
  }

  .blogbox .blogentry .blog_caption h3 {
    width: 200px;
    height: 20px;
    margin-bottom: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    line-height: 1.2;
    text-align: left;
    word-wrap: break-word;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
}

@-webkit-keyframes typing {
  0% {
    width: 0%;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}

@keyframes typing {
  0% {
    width: 0%;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}

.swiper-container {
  width: 850px;
  height: 450px;
}

.swiper-button-prev,
.swiper-button-next {
  display: none !important;
  color: var(--main-color) !important;
}

.swiper-button-prev::after,
.swiper-button-next:after {
  font-size: 24px !important;
  font-weight: bold;
}

@media screen and (max-width: 1024px) {
  .swiper-container {
    width: 630px;
  }
}

.menu .githubbox {
  width: 120px;
  height: 30px;
  border-radius: 5px;
  background-color: #fff;
}

.menu .githubbox:hover {
